@keyframes fade-in {
    from{
        opacity: 0;
    }

    to{
        opacity: 2;
    }
}

header ul a:nth-child(1){
    animation: fade-in 5s ease;
    animation-fill-mode: forwards; 
}

header ul a:nth-child(2){
    animation: fade-in 6s ease;
    animation-fill-mode: forwards; 
}

header ul a:nth-child(3){
    animation: fade-in 7s ease;
    animation-fill-mode: forwards; 
}